@font-face {
  font-family: 'Montserrat';
  src: url('../../../assets/fonts/static/Montserrat-Medium.ttf');
}

.homeContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 200px;
}
.pContainer {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 26px;
  line-height: 30px;
  color: #373737;
}
.btnContainer {
  text-decoration: none;
  cursor: pointer;
  border: none;
  margin-top: 26px;

  background-color: #bf9ee2;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.55);
  border-radius: 50%;
  padding: 20px 14px;
  margin-bottom: 30px;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 26px;
  line-height: 30px;
}
.btnContainer:hover {
  background-color: #5cd3a8;
}
